import React from "react";
import TopBanner from '../../components/TopBanner'
import Preview from '../../components/Preview'
import Helmet from "react-helmet";

export default function Isolted ({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div className="preview-feed">
      <Helmet title={`Изолирующие упражнения`} />
      <TopBanner style={isolatedStyle} title={'Изолирующие упражнения'} />
      {posts
        .filter(post => post.node.frontmatter.filtertype === 'isolated')
        .map(({ node: post }) => {
          return (
            <Preview post={post} />
          );
        })}
    </div>
  );
}

const isolatedStyle = {
  backgroundImage: 'linear-gradient(to right top, #f046a6, #ff4683, #ff5461, #fe6a40, #ed811e)'
};

export const pageQuery = graphql`
  query IsolatedQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 180)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            image
            image_2
            image_3
            image_4
            filterby
            efficiency
            injury
            bodypart
            filtertype
            type
            filterinventory
            inventory
          }
        }
      }
    }
  }
`;



